.New-app-page {
  text-align: center;
  width: 100%;
  height: 100%;
  padding: 10px;
  font-family: Arimo, Arial, sans-serif;
}

.Page-section {
  border-bottom: 1px solid #808499;
  padding: 0 0 10px 0;
}

.Page-header{
  font-size: 35px;
}

.Section-content {
  padding: 10px;
}

.Section-content > ul {
  list-style-type: none;
  overflow: hidden;
}

.Label-container {
  display: inline-block;
  width: 25%;
  text-align: right;
}

.Label-container > label {
  margin-right: 20px;
  color: #808499;
  overflow: hidden;
  text-align: right;
}

.Input-container {
  display: inline-block;
  width: 75%;
  text-align: left;
}

.Input-container > input {
  background-color: #33353d;
  width: 75%;
  padding: 10px 0 6px;
  color: #fff;
  border: 0;
  border-bottom: 1px solid #808499;
  outline: none;
}

.Select {
  background-color: #33353d;
  width: 75%;
  padding: 10px 0 6px;
  color: #fff;
  border: 0;
  border-bottom: 1px solid #808499;
  outline: none;
}

.Path-input {
  background-color: #33353d;
  width: 75%;
  padding: 10px 0 6px;
  color: #fff;
  border: 0;
  border-bottom: 1px solid #808499;
  outline: none;
}

.Input-container > input:focus {
  border-bottom: 1px solid white;
}

.Page-Footer {
  padding: 10px;
}

.Footer-Buttons-left {
  width: 50%;
  display: inline-block;
  text-align: left;
}

.Footer-Buttons-right {
  width: 50%;
  display: inline-block;
  text-align: right;
}

.Footer-Buttons-left > button {
  height: 30px;
  padding: 0 20px;
  font-size: 14px;
  background: #464854;
  color: #fff;
  border: 0!important;
  cursor: pointer;
}

.Footer-Buttons-left > button:hover {
  background-color: #52545f;
  color: #ffffff;
}

.Footer-Buttons-right > button {
  height: 30px;
  padding: 0 20px;
  font-size: 14px;
  background-color: #0a65b1;
  color: #fff;
  border: 0!important;
  cursor: pointer;
}

.Footer-Buttons-right > button:hover {
  background-color: #2375b9;
  color: #ffffff;
}

.Path-input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.folder-icon {
  margin-left: 10px;
  height: auto;
  width: 20px;
  cursor: pointer;
}



